.App{
    width: 100%;
    height: 100%;
}

@keyframes fadeIn{
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}

@keyframes fadeUp{
    0%{
        transform: translateY(30px);
    }
    100%{
        transform: translateY(0px);
    }
}

@keyframes fadeRight{
    0%{
        transform: translateX(-30px);
    }
    100%{
        transform: translateX(0px);
    }
}

@keyframes fadeDown{
    0%{
        transform: translateY(-30px);
    }
    100%{
        transform: translateY(0px);
    }
}

@keyframes fadeLeft{
    0%{
        transform: translateX(30px);
    }
    100%{
        transform: translateX(0px);
    }
}