*{
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  font-family: myFirstFont;
}

body{
  width: 100vw;
  min-height: 100vh;
  background: url('./componentDependencies/background.jpg');
  background-size: cover;
  background-position: right;
  overflow: hidden;
}

.flex{
  display: flex;
}

.justifyCenter{
  justify-content: center;
}

.alignCenter{
  align-items: center;
}

.flexColumn{
  flex-direction: column;
}

h1, h2, h3, h4, h5{
  text-align: center;
}

.fullSize{
  width: 100%;
  height: 100%;
}

.pageSection{
  width: 100vw;
  height: 100vh;
}

@font-face {
  font-family: myFirstFont;
  src: url('./fonts/KoyaSans-Thin.ttf');
  font-weight: 200;
}

@font-face {
  font-family: myFirstFont;
  src: url('./fonts/KoyaSans-ExtraLight.ttf');
  font-weight: 300;
}

@font-face {
  font-family: myFirstFont;
  src: url('./fonts/KoyaSans-Light.ttf');
  font-weight: 400;
}

@font-face {
  font-family: myFirstFont;
  src: url('./fonts/KoyaSans-Regular.ttf');
  font-weight: 600;
}

@font-face {
  font-family: myFirstFont;
  src: url('./fonts/KoyaSans-Bold.ttf');
  font-weight: 800;
}

@font-face {
  font-family: myFirstFont;
  src: url('./fonts/KoyaSans-Black.ttf');
  font-weight: 900;
}