nav{
    position: fixed;

    display: flex;
    justify-content: space-between;

    height: 50px;
    width: 100vw;

    padding: 5px 20px;
    animation: fadeBackgroundIn 0.5s ease 2.5s both;

    z-index: 2;
}

@keyframes fadeBackgroundIn{
    0%{
        background: rgba(0,0,0,0);
    }
    100%{
        background: rgba(0,0,0,0.8);
    }
}

.navElement ul li{
    list-style-type: none;
    margin: 10px;
}

.navElement ul li a{
    position: relative;
    display: block;
    width: fit-content;
    text-decoration: none;
    color: white;
    font-size: 1rem;
}

#nav_options a::before{
    position: absolute;
    content: "";
    left: -7px;
    bottom: -4px;
    width: 0%;
    height: 1rem;
    background: white;
    mix-blend-mode: exclusion;
    transition: 0.2s all ease-in-out;
    z-index: 2;
}

#nav_options{
    animation: fadeIn 0.5s ease 3s both, fadeLeft 0.5s ease 3s both;
}

#nav_options a:hover::before{
    height: 1.8rem;
    width: calc(100% + 15px);
}

#nav_trigger, #nav_trigger_trigger{
    display: none;
}

#SonicWallLogo{
    position: absolute;
    height: 25px;
    width: 155px;
    animation: zoomOut 1.5s ease-in-out 2s alternate both;
    object-fit: 50%;
    z-index: 10;
}

nav::before{
    position: absolute;
    content: "";
    background: rgb(40, 40, 40);
    height: 100vh;
    width: 100vw;
    top: 0px;
    left: 0px;
    animation: fadeIn 0.5s ease 2s reverse both, curtainRemoval 1s ease 3s both;
    z-index: 3;
}

@keyframes curtainRemoval{
    0%{
        width: 100vw;
        height: 100vh;
    }
    100%{
        width: 0vw;
        height: 0vh;
    }
}

@keyframes zoomOut {
    0%{
        height: 10vw;
        width: 62vw;
        top: calc(calc(100vh - 10vw) / 2);
        left: 19vw;
    }
    100%{
        height: 25px;
        width: 155px;
        left: 30px;
        top: 14px;
    }
}

@media only screen and (max-width: 1000px){
    #nav_options{
        position: absolute;
        height: calc(100vh - 50px);
        width: 100vw;
        background: black;
        left: 100%;
        top: 50px;
        flex-direction: column;
        justify-content: space-evenly;
        transition: 0.5s ease-in-out all;
        z-index: 10;
    }

    #nav_trigger:checked + #nav_options{
        left: 0px;
        z-index: 10;
    }

    #nav_trigger_trigger{
        display: block;
        animation: fadeIn 0.5s ease 3s both, fadeDown 0.5s ease 3s both;
    }
}