.Form{
    background: rgba(0, 0, 0, 0.5);
    border-radius: 10px;
    color: white;
    padding: 20px;
    width: fit-content;
    backdrop-filter: blur(15px);
}

.inputFields{
    justify-content: right;
    margin: 10px;
    font-size: 0.9rem;
}

.inputFields input{
    margin: 0px 10px;
    outline: none;
    border: none;
    padding: 5px 7px 3px 7px;
    font-weight: 600;
}

.connectButton{
    position: relative;
    background: none;
    border: none;
    background: black;
    color: white;
    font-weight: 600;
    border-radius: 50%;
    height: 90px;
    width: 90px;
    cursor: pointer;
}

.connectButton svg{
    position: absolute;
    height: 107%;
    width: 107%;
    z-index: -1;
    rotate: -90deg;
}

.connectButton svg circle{
    stroke: white;
    stroke-width: 7px;
    stroke-dasharray: 314;
    stroke-dashoffset: 314;
    transition: 0.5s all ease;
}

.connectButton:hover svg circle{
    stroke-dashoffset: 0;
}