.LoadingScreen{
    position: absolute;
    background: white;
    width: 100vw;
    height: 100vh;
    left: 0px;
    z-index: 5;

    animation: coverUp 1.5s ease both;
}

.LoadingStatus{
    width: clamp(50px, 50vw, 200px);
}

.LoadingStatus svg{
    margin: 20px;
    rotate: -135deg;
}

.LoadingStatus circle{
    fill: none;
    stroke: rgb(40, 40, 40);
    stroke-width: 10px;
    stroke-linecap: round;
    transition: all 0.3s ease;
}

.SignalBars{
    animation: blink 2s ease infinite alternate both;
}

.SignalBars:nth-child(1){
    stroke-dasharray: 314;
    stroke-dashoffset: 236;
    animation-delay: 1.2s;
}

.SignalBars:nth-child(2){
    stroke-dasharray: 220;
    stroke-dashoffset: 165;
    animation-delay: 0.8s;
}

.SignalBars:nth-child(3){
    stroke-dasharray: 126;
    stroke-dashoffset: 94;
    animation-delay: 0.4s;
}

.StatusTitle{
    color: rgb(40, 40, 40);
}

@keyframes blink{
    0%{
        opacity: 0.2;
    }
    30%{
        opacity: 0.2;
    }
    45%{
        opacity: 1;
    }
    55%{
        opacity: 1;
    }
    70%{
        opacity: 0.2;
    }
    100%{
        opacity: 0.2;
    }
}

@keyframes coverUp {
    0%{
        top: 100vh;
    }
    100%{
        top: 0vh;
    }
}